<template>
    <div class="swiper-banner">
        <div class="wrapper">
            <swiper :options="swiperOption" class="swiper-container" >
                <!-- slides -->
                <swiper-slide
                        class="swiper-item"
                        v-for='(item,index) of swiperList'
                        :key='index'
                >
                    <router-link :to="{name: 'storyDetail',params: {id: item.id}}">
                        <img class='swiper-img' :src=item.image />
                    </router-link>

                </swiper-slide>
                <!-- Optional controls ,显示小点-->
                <div class="swiper-pagination"  slot="pagination"></div>
                <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
                <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
            </swiper>
        </div>
    </div>
</template>

<script>
    import 'swiper/dist/css/swiper.css';
    import { swiper, swiperSlide } from 'vue-awesome-swiper';

    export default {
        name: 'SwiperBanner',
        components: {
            swiper,
            swiperSlide
        },
        data() {
            return {
                swiperList: [],
                swiperOption: {
                    loop : true,
                    spaceBetween: 30,
                    effect: 'fade',
                    autoplay: {
                        delay: 3000,
                        disableOnInteraction: false
                    },
                    pagination: {
                        el: '.swiper-pagination',
                        clickable: true
                    },
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev'
                    }
                }
            }
        },
        methods :{
            getBannerList(){
                this.axios({
                    method: 'get',
                    url: 'http://123.57.11.135/getBra',
                    responseType: "json"
                }).then(( response ) => {
                    this.swiperList = response.data.data
                    window.console.log(this.swiperList)
                }).catch((error) => {
                    window.console.log(error);
                })
            }
        },
        created() {
            this.getBannerList()
        }
    }
</script>

