                        >
                            <li
                                    v-for="(item,index) of oneLevelSelect"
                                    :key="index"
                                    :index="index"
                                    @click="clickOneLevel($event)"
                                    :class="[item.status ? 'select-one-level-item' : '']"
                            >
                                {{item.value}}
                            </li>
                        </ul>
                    </div>
                    <div
                            class="goods-specs"
                            v-if="twoLevelSelect[oneLevelKey]"
                    >
                        <span
