                                @click="showSelectBox('goods-specs-list')"
                        >
                            {{twoLevelSelect[oneLevelKey][twoLevelKey].value}}
                        </span>
                        <ul class="goods-specs-list">
                            <li
                                    v-for="(item,index) of twoLevelSelect[oneLevelKey]"
                                    :key="index"
                                    :index="index"
                                    @click="clickTwoLevel($event)"
                                    :class="[item.status ? 'select-two-level-item' : '']"
                            >
                                {{item.value}}
