<script>
    import Header from "../components/Header";
    import Footer from "../components/Footer";
    import 'swiper/dist/css/swiper.css';
    import { swiper, swiperSlide } from 'vue-awesome-swiper';

    export default {
        name: "Detail",
        components: {Footer, Header,swiper, swiperSlide},
        data() {
            return {
                selectTagKey: 'classify',
                tagList: [
                    {
                        key: 'home',
                        name: '首页',
                        href: '/',
                    }, {
                        key: 'classify',
                        name: '分类',
                        href: '/classify/id/1',

                    },{
                        key: 'story',
                        name: '品牌故事',
                        href: '/story',
                    },{
                        key: 'about',
                        name: '关于我们',
                        href: '/about',
                    }
                ],
                menuLevel: [],
                oneLevelKey: 0,
                oneLevelSelect: [],
                twoLevelKey: 0,
                twoLevelSelect: [],
                selectThumbnailKey: 0,
                thumbnailList: [],
                hideSmallPicKey: 0,
                content: []
            }
        },
        methods: {
            getContent(){
                this.axios({
                    method: 'get',
                    url: 'http://123.57.11.135/getproFi?id=1',
                    responseType: 'json'
                }).then((response) => {
                    this.content = response.data.data

                    this.menuLevel = response.data.data.type

                    /* 处理规格选项 */
                    for (let i in response.data.data.specs['a-level']){
                        let tempA = response.data.data.specs['a-level'][i]
                        tempA['status'] = i == 0 ? true : false
                        this.oneLevelSelect.push(tempA)
                    }

                    for(let i in response.data.data.specs['b-level']){
                        let BLevel = [];
                        for (let j in response.data.data.specs['b-level'][i]){
                            let tempB = {
                                status: j == 0 ? true : false,
                                value: response.data.data.specs['b-level'][i][j],
                                key: j,
                            }
                            BLevel.push(tempB)
                        }
                        this.twoLevelSelect.push(BLevel)
                    }


                    let aArr = response.data.data.de_image.list1
                    aArr.push(response.data.data.de_image.list2[0])
                    aArr.push(response.data.data.de_image.list2[1])
                    /* 处理小图列表 */
                    for(let i in aArr){
                        let tempArr = {
                            key: aArr[i].key,
                            image: aArr[i].image,
                            status: i == 0 ? true : false,
                        };
                        this.thumbnailList.push(tempArr)
                    }
                }).catch(() => {

                })
            },
            showSelectBox(name) {
                document.getElementsByClassName(name)[0].style = 'display:block';
            },
            clickOneLevel(e) {
                let yKey = document.getElementsByClassName('select-one-level-item')[0].getAttribute('index')
                let cKey = e.target.getAttribute('index')
                this.oneLevelSelect[yKey].status = false
                this.oneLevelSelect[cKey].status = true
                this.oneLevelKey = cKey
                this.twoLevelKey = 0
                document.getElementsByClassName('goods-type-list')[0].style = 'display: none'
            },
            clickTwoLevel(e) {
                let yKey = document.getElementsByClassName('select-two-level-item')[0].getAttribute('index')
                let cKey = e.target.getAttribute('index')
                this.twoLevelSelect[this.oneLevelKey][yKey].status = false
                this.twoLevelSelect[this.oneLevelKey][cKey].status = true
                this.twoLevelKey = e.target.getAttribute('index')
                document.getElementsByClassName('goods-specs-list')[0].style = 'display: none'
            },
            showBigPic(e) {
                let yKey = document.getElementsByClassName('select-item')[0].getAttribute('index')
                let cKey = e.currentTarget.getAttribute('index')

                this.thumbnailList[yKey].status = false
                this.thumbnailList[cKey].status = true

                this.selectThumbnailKey = e.currentTarget.getAttribute('index')
            },
            hideSmallPic(type) {
                if(type == 'up'){
                    if(this.hideSmallPicKey == 0){
                        return false;
                    }

                    this.hideSmallPicKey = this.hideSmallPicKey - 1
                    let marginTop = this.hideSmallPicKey * 114
                    document.getElementsByClassName('small-pic-list')[0].firstChild.style = 'margin-top: -'+marginTop+'px';

                } else {
                    if ((this.thumbnailList.length - 3) == this.hideSmallPicKey) {
                        document.getElementsByClassName('small-pic-more')[0].style = 'top: 1px;transform:rotate(180deg);';
                        return false;
                    }
                    this.hideSmallPicKey = this.hideSmallPicKey + 1
                    let marginTop = this.hideSmallPicKey * 114
                    document.getElementsByClassName('small-pic-list')[0].firstChild.style = 'margin-top: -'+marginTop+'px';

                }
            }
        },
        mounted() {
            this.getContent()
        },
        created() {
            document.getElementsByTagName('html')[0].scrollTop = 0;
        }
    }
</script>

