<template>
    <div class="story">
        <!-- 头部Tag  -->
        <Header
                :tagList="tagList"
                :selectTagKey="selectTagKey"
        >
        </Header>
        <!-- end -->

        <div class="story-total">
            <span>1090 故事</span>
            <span>我们真正关心的是消费者的健康</span>
            <span>给大家最好、最健康、最能持续发展的生活方式</span>
        </div>

        <div class="story-box">
            <ul class="story-list">
                <li
                        v-for="(item,index) of storyList"
                        :key="index"
                        :class="[item.key == 3 || item.key == 4 ? 'story-item-type-big' : 'story-item-type-small']"
                >
                    <router-link
                            class="story-item-link"
                            :to="{name: 'storyDetail', params: {id: item.id}}"
                    >
                        <span
                                :class="[item.key == 3 || item.key == 4 ? 'big-pic' : 'small-pic']"
                        >
                            <img :src=item.image />
                        </span>
                        <span
                                :class="[item.key == 3 || item.key == 4 ? 'big-title' : 'small-title']"
                        >
                            {{item.title}}
                        </span>
                        <span
                                :class="[item.key == 3 || item.key == 4 ? 'big-time' : 'small-time']"
                        >
                            {{item.time}}
                        </span>
                    </router-link>
                </li>
            </ul>
            <div class="list-end">没有更多了~</div>
        </div>

        <!-- 返回头部 -->
        <ReturnTop></ReturnTop>
        <!-- end -->

        <!-- footer -->
        <Footer></Footer>
        <!--  end -->
    </div>
</template>

<script>
    import Header from "../components/Header";
    import Footer from "../components/Footer";
    import ReturnTop from "../components/ReturnTop";

    export default {
        name: "Story",
        components:{ Header, Footer, ReturnTop },
        data() {
            return {
                selectTagKey: 'story',
                tagList: [
                    {
                        key: 'home',
                        name: '首页',
                        href: '/',
                    }, {
                        key: 'classify',
                        name: '分类',
                        href: '/classify/id/1',
                    },{
                        key: 'story',
                        name: '品牌故事',
                        href: '/story',
                    },{
                        key: 'about',
                        name: '关于我们',
                        href: '/about',
                    }
                ],
                storyList: [],
                storyListEnd: false,
                pagination: [],
                loading: false
            }
        },
        methods: {
            getStoryList() {
                this.axios({
                    method: 'get',
                    url: 'http://123.57.11.135/getserbLi',
                    params: {
                        id: 1,
                        current: 1,
                        pagesize: 5,
                    },
                    responseType: 'json',
                }).then((response) => {
                    let array = response.data.data.list;
                    for (let i=0;i<array.length;i++){
                        array[i]['key'] = i;
                        this.storyList.push(array[i])
                    }
                    this.pagination = response.data.data.pagination
                }).catch(() => {

                })
            },
            loadMoreData() {
                if(this.storyListEnd === true){
                    return false
                }

                let wpHeight = window.screen.availHeight;
                let scrollTop = document.documentElement.scrollTop;
                let lastEleTop = document.getElementsByClassName('story-list')[0].lastChild.offsetTop;

                if((scrollTop + wpHeight) > lastEleTop && this.loading === false){
                    this.loading = true
                    this.axios({
                        method: 'get',
                        url: 'http://123.57.11.135/getserbLi',
                        params: {
                            id: 1,
                            current: Number(this.pagination.current) + 1,
                            pagesize: this.pagination.pageSize,
                        },
                        responseType: 'json'
                    }).then((response) => {
                        let array = response.data.data.list;

                        if(array.length < 1){
                            this.storyListEnd = true
                            return false
                        }
                        for (let i=0;i<array.length;i++){
                            array[i]['key'] = i;
                            this.storyList.push(array[i])
                        }
                        this.pagination = response.data.data.pagination
                        this.loading = false
                    }).catch(() => {

                    })
                }
        },
        },
        mounted() {
            window.addEventListener('scroll',this.loadMoreData)
        },
        created() {
            document.getElementsByTagName('html')[0].scrollTop = 0;
            this.getStoryList()
        }
    }
</script>

