<script>
    import Header from "../components/Header";
    import Footer from "../components/Footer";
    import ReturnTop from "../components/ReturnTop";

    export default {
        name: "StoryDetail",
        components: { Header, Footer, ReturnTop },
        data() {
            return {
                selectTagKey: 'story',
                tagList: [
                    {
                        key: 'home',
                        name: '首页',
                        href: '/',
                    }, {
                        key: 'classify',
                        name: '分类',
                        href: '/classify/id/1',
                    },{
                        key: 'story',
                        name: '品牌故事',
                        href: '/story',
                    },{
                        key: 'about',
                        name: '关于我们',
                        href: '/about',
                    }
                ],
                content: []
            }
        },
        methods: {
            showGoodsLink(e) {
                e.currentTarget.getElementsByClassName('goods-link')[0].style = 'display: block';
            },
            hideGoodsLink(e) {
                e.currentTarget.getElementsByClassName('goods-link')[0].style = 'display: none';
            },
            getContent() {
                this.axios({
                    method: 'get',
                    url: 'http://123.57.11.135/getserbFi',
                    params: {
                        id: 1,
                    },
                    responseType: 'json'
                }).then((response) => {
                  this.content = response.data.data
                  window.console.log(this.content)
                }).catch(() => {

                })
            }
        },
        created() {
            document.getElementsByTagName('html')[0].scrollTop = 0;
            this.getContent()
        }
    }
</script>

