<template>
	<div class="header">
		<div class="box">
			<div class="logo">
				<img src="../assets/logo.png" />
			</div>

			<div class="tag-list">
				<span
						class="tag"
						v-for="(item, index) of tagList"
						:key="index"
						:class="[item.key === selectTagKey ? 'select-status' : '']"
				>
					<router-link :to=item.href>{{item.name}}</router-link>
					<i></i>
				</span>
			</div>

			<div class="search">
				<input id="search-goods" type="text" v-model="searchWord" placeholder="搜索商品" />
				<i @click="clickSearch"></i>
			</div>
		</div>
	</div>

</template>

<script>
	export default {
		props: ['tagList','selectTagKey'],
		data() {
			return {
				searchWord: '',
			}
		},
		methods: {
			clickSearch() {
				this.$router.push({
					name: 'search',
					params: {
						word: this.searchWord,
					}
				})
			}
		},
		created() {
			this.searchWord = this.$route.params.word
		}
	}
</script>

