<template>
    <div class="return-top" @click="returnTop">TOP</div>
</template>

<script>
    export default {
        name: "ReturnTop",
        methods: {
            returnTop() {
                document.getElementsByTagName('html')[0].scrollTop = 0;
            }
        }
    }
</script>

<style scoped>
    .return-top{
        display: block;
        width: 70px;
        height: 92px;
        line-height: calc(92px + 23px);
        text-align: center;
        position: fixed;
        right: 0;
        top: 70%;
        background: url("../assets/back-top-icon.png") top center rgba(246,111,0,1) no-repeat;
