<template>
    <div class="classify">
        <!-- 头部Tag  -->
        <Header
                :tagList="tagList"
                :selectTagKey="selectTagKey"
        >
        </Header>
        <!-- end -->

        <div class="classify-level">
            <div class="header-box">
                <div class="name">分类</div>
                <div class="level-list">
                    <span>
                        <router-link :to="{name: 'classify', params: {id: 1}}">分类</router-link>
                            >
                        <router-link :to="{name: 'classify', params: {id: goodsList[0].type[0].id}}">
                            {{goodsList[0].type[0].type}}
                        </router-link>
                    </span>
                </div>
                <div class="goods-sort">
                    <span
                            class="select-sort-type"
                            @click="showSortList"
                    >
                        {{sortList[selectSortKey].val}}
                    </span>
                    <ul class="sort-list">
                        <li
                            v-for="(item,index) of sortList"
                            :key="index"
                            :index="item.key"
                            :sort-key="item.sortKey"
                            :class="[item.status ? 'select-sort-item' : '']"
                            @click="selectSortType($event)"
                        >
                            {{item.val}}
                        </li>
                    </ul>
                </div>
                <div class="total-goods">{{pagination.count}}个商品</div>
            </div>

            <div class="goods-box">
                <div class="left-class">
                    <ul
                            class="class-list"
                            v-for="(arr,key) of classList"
                            :key="key"
                    >
                        <li
                                v-for="(item,index) of arr"
                                :key="index"
                                :class-id=item.id
                                :one-key="key"
                                :two-key="index"
                                :class="[item.status ? 'select-class-status' : '']"
                        >
                                <span
                                        :one-key="key"
                                        :two-key="index"
                                        :class-id=item.id
                                        @click="switchClass($event)"
                                >
                                    {{item.title}}
                                </span>
                        </li>

                    </ul>
                </div>
                <div class="right-goods">
                    <ul class="goods-list">
                        <li
                                class="goods-item"
                                @mouseover="showGoodsLink($event)"
                                @mouseout="hideGoodsLink($event)"
                                v-for="(item,index) of goodsList"
                                :key="index"
                        >
                            <div class="goods-info">
                                <span class="goods-pic">
                                    <img :src=item.image />
                                </span>
                                <span class="goods-summary">{{item.brief}}</span>
                                <span class="goods-name">{{item.title}}</span>
                            </div>
                            <div class="goods-link">
                                <router-link class="goods-link-btn" :to="{name: 'detail'}">查看详情</router-link>
                            </div>
                        </li>
                    </ul>
                    <span class="list-end">没有更多了~</span>
                </div>
            </div>
        </div>

        <!-- 返回头部 -->
        <ReturnTop></ReturnTop>
        <!-- end -->

        <!-- footer -->
        <Footer></Footer>
        <!--  end -->
    </div>
</template>

<script>
    import Header from "../components/Header";
    import Footer from "../components/Footer";
    import ReturnTop from "../components/ReturnTop";

    export default {
        name: "Classify",
        components: {
            ReturnTop,
            Header,
            Footer
        },
        data() {
            return {
                selectSortKey: 0,
                sortList: [
                    {
                        key: 0,
                        val: '默认',
                        sortKey: 2,
                        status: true
                    },
                    {
                        key: 1,
                        val: '上架时间升序',
                        sortKey: 1,
                        status: false
                    },
                    {
                        key: 2,
                        val: '上架时间降序',
                        sortKey: 2,
                        status: false
                    }
                ],
                sortKey: 2,
                selectTagKey: 'classify',
                tagList: [
                    {
                        key: 'home',
                        name: '首页',
                        href: '/',
                    }, {
                        key: 'classify',
                        name: '分类',
                        href: '/classify/id/1',
                    },{
                        key: 'story',
                        name: '品牌故事',
                        href: '/story',
                    },{
                        key: 'about',
                        name: '关于我们',
                        href: '/about',
                    }
                ],
                classList: [],
                goodsList: [],
                goodsListEnd: false,
                pagination: [],
                classId: 1,
                loading: false
            }
        },
        methods: {
            showGoodsLink(e) {
                e.currentTarget.getElementsByClassName('goods-link')[0].style = 'display: block';
            },
            hideGoodsLink(e) {
                e.currentTarget.getElementsByClassName('goods-link')[0].style = 'display: none';
            },
            getClassList() {
                this.axios({
                    method: 'get',
                    url: 'http://123.57.11.135/getserLi',
                    responseType: 'json'
                }).then((response) => {
                    for(let i in response.data.data){
                        let tempArr = [];
                        for(let j in response.data.data[i]){
                            let tempItem = {
                                id: response.data.data[i][j].id,
                                title: response.data.data[i][j].title,
                                pid: response.data.data[i][j].pid,
                                status: this.classId == response.data.data[i][j].id ? true : false
                            };
                            tempArr.push(tempItem)
                        }
                        this.classList.push(tempArr)
                    }
                }).catch(() => {

                })
            },
            getGoodsList() {
                this.axios({
                    method: 'get',
                    url: 'http://123.57.11.135/getproLi',
                    params: {
                        id: this.classId,
                        current: 1,
                        pagesize: 9
                    },
                    responseType: 'json',
                }).then((response) => {
                    this.goodsList = response.data.data.list
                    this.pagination = response.data.data.pagination
                }).catch(() => {

                })
            },
            showSortList() {
                document.getElementsByClassName('sort-list')[0].style = 'display:block'
            },
            selectSortType(e) {
                let yKey = document.getElementsByClassName('select-sort-item')[0].getAttribute('index')
                let cKey = e.target.getAttribute('index')
                this.sortList[yKey].status = false
                this.sortList[cKey].status = true
                this.selectSortKey = cKey
                this.sortKey = document.getElementsByClassName('select-sort-item')[0].getAttribute('sort-key')
                document.getElementsByClassName('sort-list')[0].style = 'display: none'

                this.axios({
                    method: 'get',
                    url: 'http://123.57.11.135/getproLi',
                    params: {
                        id: this.classId,
                        current: 1,
                        pagesize: 9,
                        sort: this.sortKey
                    },
                    responseType: 'json',
                }).then((response) => {
                    this.goodsList = response.data.data.list
                    this.pagination = response.data.data.pagination
                }).catch(() => {

                })
            },
            switchClass(e) {
                let yOneKey = document.getElementsByClassName('select-class-status')[0].getAttribute('one-key')
                let yKey = document.getElementsByClassName('select-class-status')[0].getAttribute('two-key')
                let cOneKey = e.currentTarget.getAttribute('one-key')
                let cKey = e.currentTarget.getAttribute('two-key')
                let classId = e.currentTarget.getAttribute('class-id')
                window.console.log(yOneKey)
                window.console.log(yKey)
                window.console.log(cOneKey)
                window.console.log(cKey)
                this.classList[yOneKey][yKey].status = false
                this.classList[cOneKey][cKey].status = true
                this.$router.push({
                    params: {
                        id: classId
                    }
                })
            },
            loadMoreData() {
                if(this.goodsListEnd === true){
                    return false
                }

                let wpHeight = window.screen.availHeight;
                let scrollTop = document.documentElement.scrollTop;
                let lastEleTop = document.getElementsByClassName('goods-list')[0].lastChild.offsetTop;

                if((scrollTop + wpHeight) > lastEleTop && this.loading === false){
                    this.loading = true
                    this.axios({
                        method: 'get',
                        url: 'http://123.57.11.135/getproLi',
                        responseType: 'json',
                        params: {
                            pagesize: this.pagination.pageSize,
                            current: Number(this.pagination.current) + 1,
                            id: this.classId,
                            sort: this.sortKey
                        }
                    }).then((response) => {
                        let array = response.data.data.list;

                        if(array.length < 1){
                            this.goodsListEnd = true
                            this.loading = false
                            return false
                        }
                        for (let i=0;i<array.length;i++){
                            this.goodsList.push(array[i])
                        }
                        this.pagination = response.data.data.pagination
                        this.loading = false
                    }).catch(() => {

                    })
                }
            }
        },
        watch: {
            $route(){
                this.classId = this.$route.params.id
                document.getElementsByTagName('html')[0].scrollTop = 0;
                this.goodsListEnd = false

                this.getGoodsList()
            }
        },
        mounted() {
            window.addEventListener('scroll',this.loadMoreData)
            this.classId = this.$route.params.id
        },
        created() {
            document.getElementsByTagName('html')[0].scrollTop = 0;
            this.getClassList()
            this.getGoodsList()
        }
    }
</script>

