<template>
    <div class="classify">
        <!-- 头部Tag  -->
        <Header
                :tagList="tagList"
                :selectTagKey="selectTagKey"
        >
        </Header>
        <!-- end -->

        <div class="classify-level">
            <div class="header-box">
                <div class="name">分类</div>
                <div class="search-result-summary">
                    <span
                            v-if="goodsList.length > 0"
                    >
                        您搜索的“{{searchWord}}”共找到 {{pagination.count}} 个产品
                    </span>
                    <span
                           v-else
                    >找到0个结果
                    </span>
                </div>
                <div class="goods-sort">
                    <span
                            class="select-sort-type"
                            @click="showSortList"
                    >
                        {{sortList[selectSortKey].val}}
                    </span>
                    <ul class="sort-list">
                        <li
                                v-for="(item,index) of sortList"
                                :key="index"
                                :index="item.key"
                                :sort-key="item.sortKey"
                                :class="[item.status ? 'select-sort-item' : '']"
                                @click="selectSortType($event)"
                        >
                            {{item.val}}
                        </li>
                    </ul>
                </div>
                <div class="total-goods">{{pagination.count}}个商品</div>
            </div>

            <div class="goods-box">
                <div class="left-class">
                    <ul
                            class="class-list"
                            v-for="(arr,key) of classList"
                            :key="key"
                    >
                        <li
                                v-for="(item,index) of arr"
                                :key="index"
                                :class-id=item.id
                        >
                            <router-link
                                    :to="{name: 'classify', params: {id: item.id}}"
                            >
                                {{item.title}}
                            </router-link>
                        </li>

                    </ul>
                </div>
                <div class="right-goods">
                    <div
                            class="null-search-result"
                            v-if="goodsList.length < 1"
                    >
                        <p>非常抱歉，我们没有找到您要搜索的产品“{{searchWord}}”。</p>
                        <p>请尝试输入其它关键词，希望您找到满意的产品。</p>
                    </div>
                    <ul
                            class="goods-list"
                            v-else
                    >
                        <li
                                class="goods-item"
                                @mouseover="showGoodsLink($event)"
                                @mouseout="hideGoodsLink($event)"
                                v-for="(item,index) of goodsList"
                                :key="index"
                        >
                            <div class="goods-info">
                                <span class="goods-pic">
                                    <img :src=item.image />
                                </span>
                                <span class="goods-summary">{{item.brief}}</span>
                                <span class="goods-name">{{item.name}}</span>
                            </div>
                            <div class="goods-link">
                                <router-link class="goods-link-btn" :to="{name: 'detail', params: {id: item.id}}">查看详情</router-link>
                            </div>
                        </li>
                    </ul>
                    <span class="list-end">没有更多了~</span>
                </div>

                <!-- 推荐模块 -->
                <RecommendBlock
                    :recommendList="recommendList"
                >
                </RecommendBlock>
            </div>
        </div>

        <!-- 返回头部 -->
        <ReturnTop></ReturnTop>
        <!-- end -->

        <!-- footer -->
        <Footer></Footer>
        <!--  end -->
    </div>
</template>

<script>
    import Header from "../components/Header";
    import Footer from "../components/Footer";
    import ReturnTop from "../components/ReturnTop";
    import RecommendBlock from "../components/RecommendBlock";

    export default {
        name: "Search",
        components: {
            ReturnTop,
            Header,
            Footer,
            RecommendBlock
        },
        data() {
            return {
                selectSortKey: 0,
                sortList: [
                    {
                        key: 0,
                        val: '默认',
                        sortKey: 2,
                        status: true
                    },
                    {
                        key: 1,
                        val: '上架时间升序',
                        sortKey: 1,
                        status: false
                    },
                    {
                        key: 2,
                        val: '上架时间降序',
                        sortKey: 2,
                        status: false
                    }
                ],
                sortKey: 2,
                selectTagKey: 'classify',
                searchWord: '',
                tagList: [
                    {
                        key: 'home',
                        name: '首页',
                        href: '/',
                    }, {
                        key: 'classify',
                        name: '分类',
                        href: '/classify/id/1',
                    },{
                        key: 'story',
                        name: '品牌故事',
                        href: '/story',
                    },{
                        key: 'about',
                        name: '关于我们',
                        href: '/about',
                    }
                ],
                classList: [],
                goodsList: [],
                goodsListEnd: false,
                pagination: [],
                recommendList: [],
                loading: false
            }
        },
        methods: {
            showGoodsLink(e) {
                e.currentTarget.getElementsByClassName('goods-link')[0].style = 'display: block';
            },
            hideGoodsLink(e) {
                e.currentTarget.getElementsByClassName('goods-link')[0].style = 'display: none';
            },
            getClassList() {
                this.axios({
                    method: 'get',
                    url: 'http://123.57.11.135/getserLi',
                    responseType: 'json',
                }).then((response) => {
                    this.classList = response.data.data
                    window.console.log(this.classList)
                }).catch(() => {

                })
            },
            getSearchList() {
                this.axios({
                    method: 'post',
                    url: 'http://123.57.11.135/getproSe',
                    params: {
                        name: this.searchWord,
                        current: 1,
                        pagesize: 9,
                    },
                    responseType: 'json'
                }).then((response) => {
                    this.goodsList = response.data.data.pSear.list
                    this.recommendList = response.data.data.pReco.list
                    this.pagination = response.data.data.pSear.pagination
                }).catch(() => {

                })
            },
            showSortList() {
                document.getElementsByClassName('sort-list')[0].style = 'display:block'
            },
            selectSortType(e) {
                let yKey = document.getElementsByClassName('select-sort-item')[0].getAttribute('index')
                let cKey = e.target.getAttribute('index')
                this.sortList[yKey].status = false
                this.sortList[cKey].status = true
                this.selectSortKey = cKey
                this.sortKey = document.getElementsByClassName('select-sort-item')[0].getAttribute('sort-key')
                document.getElementsByClassName('sort-list')[0].style = 'display: none'

                this.axios({
                    method: 'get',
                    url: 'http://123.57.11.135/getproLi',
                    params: {
                        id: this.classId,
                        current: 1,
                        pagesize: 9,
                        sort: this.sortKey
                    },
                    responseType: 'json',
                }).then((response) => {
                    this.goodsList = response.data.data.list
                    this.pagination = response.data.data.pagination
                }).catch(() => {

                })
            },
            loadMoreData() {
                if(this.loading === true){
                    return false
                }

                if(this.goodsList.length < 1){
                    this.goodsListEnd = true
                }

                if(this.goodsListEnd === true){
                    return false
                }

                let wpHeight = window.screen.availHeight;
                let scrollTop = document.documentElement.scrollTop;
                let lastEleTop = document.getElementsByClassName('goods-list')[0].lastChild.offsetTop;

                if((scrollTop + wpHeight) > lastEleTop && this.loading === false){
                    this.loading = true
                    this.axios({
                        method: 'post',
                        url: 'http://123.57.11.135/getproSe',
                        params: {
                            name: this.searchWord,
                            pagesize: this.pagination.pageSize,
                            current: Number(this.pagination.current) + 1,
                        },
                        responseType: 'json',
                    }).then((response) => {
                        let array = response.data.data.pSear.list;

                        if(array.length == 0){
                            this.loading = false
                            this.goodsListEnd = true
                            return false
                        }
                        for (let i=0;i<array.length;i++){
                            this.goodsList.push(array[i])
                        }
                        this.pagination = response.data.data.pSear.pagination
                        this.loading = false
                    }).catch(() => {

                    })
                }
            }
        },
        watch: {
            $route(){
                this.searchWord = this.$route.params.word
                document.getElementsByTagName('html')[0].scrollTop = 0;
                this.goodsListEnd = false

                this.getSearchList()
            }
        },
        mounted() {
            window.addEventListener('scroll',this.loadMoreData)
        },
        created() {
            document.getElementsByTagName('html')[0].scrollTop = 0;
            this.searchWord = this.$route.params.word
            this.getClassList()
            this.getSearchList()
        }
    }
</script>

